function PhotoPage({ classes, data, pageContext }) {
	// const people = mock.map(a => <Profile photoURL="https://static01.nyt.com/images/2010/12/07/blogs/thechoice-cherry-creek-campbell/thechoice-cherry-creek-campbell-blog480.jpg"
	// {...a} />);
	console.log(data);
	const map = new Map();
	for (const o of data.allFile.edges) {
		map.set(o.node.relativePath, o.node.childImageSharp.fluid);
	}
	const { officers } = pageContext;
	const people = officers.map(o => ({ ...o, fluidImg: map.get(o.photoURL) }));
	const profiles = people.map(a => <Profile {...a}/>);
	return (
		<React.Fragment>
			<div className={classes.container}>
				<img src={Head} style={{ width: '100%' }}/>
				<div className={classes.content}>
					{teamIntro}
				</div>
				<div className={classes.team}> The Team </div>
				<div className={classes.teamContainer}>
					{profiles}
				</div>
			</div>
		</React.Fragment>
	);
}

PhotoPage.propTypes = {
	classes: PropTypes.object.isRequired
};

export default withStyles(styles)(PhotoPage);

export const pageQuery = graphql`
	query OfficerPhotos($urls: [String]!) {
		allFile(filter: {relativePath: {in: $urls}}) {
			edges {
				node {
					relativePath
					childImageSharp {
						fluid(maxWidth: 200) {
							...GatsbyImageSharpFluid
						}
					}
				}
			}
		}
	}
`;
